* {
  margin: 0;
  padding: 0;
}
#Author-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 35rem;
  box-sizing: border-box;
  margin-top: 30px;
  padding: 10px;

}

#Author-content {
  width: 70%;
  height: 100%;
}
#Author-content-headline {
  text-align: center;
  font-size: 35px;
  margin: 8px;
  padding: 5px;
  border: 1px solid rgb(0, 0, 0);
}

#Author-content-description {
  font-size: 17px;
  margin: 10px;
  padding: 10px;
}

#Author-Image {
  box-sizing: border-box;
  margin: 8px;
  width: 30%;
  height: 100%;
}

#Author-Image img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
