*{
  margin: 0;
  padding: 0;
}
#container {
  display: flex;
  justify-self: center;
  margin: auto;
  background-color: rgb(40, 169, 255);
  height: 36rem;
  width: 10rem;
}

#cont {
  background-color: #171717;
}
#service_category_imgs {
  position: absolute;
  margin-left: 160px;
  margin-top: 30px;
  width: 30rem;
  height: 20rem;
  background: url("./img/women-1209678.jpg") center center/cover;
}

#service_category_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  background-color: #800068f2;
  width: 30rem;
  height: 15rem;
  margin-left: 35rem;
  margin-top: 19rem;
}
#icon {
  position: absolute;
  z-index: 1;
  width: 17rem;
  height: 17rem;
  margin-left: 41rem;
  margin-top: 5rem;
  filter: invert(1);
}

#second_container {
  display: flex;
  justify-self: center;
  margin: auto;
  background-color: rgb(255, 187, 40);
  height: 7rem;
  width: 65rem;
}
#services_second_container {
  display: flex;
  margin-left: 14rem;
  height: 45rem;
  width: 6rem;
  background-color: rgb(255, 135, 80);
}

.services_content_two {
  background-color: #171717;
}

#service_second_category_imgs {
  position: absolute;
  margin-left: 160px;
  margin-top: 30px;
  width: 30rem;
  height: 20rem;
  background: url("./img/brooke-cagle-ICTKcvnXx_8-unsplash.jpg") center
    center/cover;
}

#service_second_category_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  background-color: #800068f2;
  width: 30rem;
  height: 15rem;
  margin-left: 35rem;
  margin-top: 19rem;
}

#service_second_category_text_two {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  background-color: #006680f2;
  width: 30rem;
  height: 15rem;
  margin-left: 21rem;
  margin-top: 32rem;
}
#icon_two {
  position: absolute;
  z-index: 1;
  width: 17rem;
  height: 17rem;
  margin-left: 23rem;
  margin-top: 17rem;
  filter: invert(1);
}

#services_cards {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  background-color:#171717;
  height: 37rem;
  z-index: 1;
  
}
.services_card {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 14rem;
  height: 16rem;
  margin: 5px;
  margin-top: 10rem;
  background-color: #f7c24d;
  border-radius: 10px;

}

.services_card_two {
  margin-top: 14rem;
}

.services_card_headline {
  text-align: center;
  margin-top: 5px;
}
.services_card_desc {
  text-align: center;
  margin-top: 20px;
}

.services_card_price {
  text-align: center;
  margin-top: 45px;
}
.purchase_button {
  text-align: center;
  margin: auto;
  width: 8rem;
  height: 39px;
  outline: none;
  border-radius: 9px;
  border: none;
  font-size: 22px;
  color: white;
  background-color: #171717;
}

