#section{
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 100%;
    height: 40rem;
    /* border: 4px solid green; */
}



.section-item{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.section-item h1{
    padding: 3px;
    margin: 0 auto;
    font-size: 45px;
    color: white;
}

.description{
    padding: 3px;
    text-align: center;
    font-size: 15px;
    color: white;
}

#section::before{
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    background: url("./imgs/himiway-bikes-ZZ4KpYW86Ms-unsplash.jpg") no-repeat center center/cover;
    z-index: -1;
}



#faltu{
    font-size: 60px;
}