#Cards-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  margin-top: 50px;
  box-sizing: border-box;
}



.cards {
  width: 100%;
  height: 30rem;
  margin: 3px;
  border-radius: 15px;
  box-sizing: border-box;
}

.card1{
  background: url("./imgs/boardwalk-569314.jpg") center
    center/cover no-repeat;
    opacity: 0.9;
    z-index: 1;
}
.card2{
  background: url("./imgs/roof-1028255.jpg") center

    center/cover no-repeat;
    opacity: 0.9;
    z-index: 1;
}
.card3{
  background: url("./imgs/mosque-6231915.jpg") center
    center/cover no-repeat;
    opacity: 0.9;
    z-index: 1;
}

.Card-content {
  display: flex;
  flex-direction: column;
  margin: auto auto;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

}

.Card-headLine {
  font-size: 35px;
  padding: 5px;
  margin: 10px;
  text-shadow: 0px 0px 2px rgb(168, 166, 166);
}

.Card-Description {
  font-size: 15px;
  text-align: center;
  padding: 5px;
  margin: 5px;
  /* border: 2px solid red; */
}

.Card-Button {
  padding: 5px;
  width: 7rem;
  height: 2rem;
  font: 20px;
  margin: auto;
  margin-top: 40px;
  outline: none;
    border: none;
    border-radius: 5px;
    background-color: rgb(0, 0, 0);
    color: white;
}

#Card-Footer {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin-top: 35%;
  width: 100%;
}

.Card-Footer-Box{
    display: flex;
    flex-direction: row;
    width: 50%;
    height: 30%;
    margin: 50px;
    
    /* border: 2px solid green; */
}

.Card-Footer-icon{
    margin-right: 5px;
    width: 20px;
    height: 20px;
    filter: invert(1);
}

