#Footer-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 25rem;
    box-sizing: border-box;
    background-color: black;
}

#Footer-container-links{
    display: flex;
    flex-direction: row;
    height: 80%;
    border: 2px solid rgb(255, 255, 255);
}

.Footer-link{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 50px;
}
.Footer-link a{
    margin: 5px;
    padding: 5px;
    text-decoration: none;
    color: white;
}

#Footer-social-icon{
margin-left: 35rem;    
height: 6rem;
width: 18%;
margin-top: 50px;
box-sizing: border-box;
}

#Footer-social-icon h1{
    color:white;
    text-align: center;
}

.Footer-icon{
    width: 25px;
    height: 25px;
    margin: 17px;
    cursor: pointer;   
}

#Footer-Bottom{
    margin: auto;
    text-align: center;
    color: white;
}