.container1 ,.container2 {
  display: flex;
  flex-direction: column;
}

.content_layout1,.content_layout2 {
  display: flex;
}

.box {
  width: 25rem;
  height: 20rem;
  margin: auto;
  margin-top: 2rem;
  background-color: black;
}
.content_layout1_img {
  width: 50rem;
  height: 30rem;

}

.content_layout1_img img{
  position: absolute;
  top: 9rem;
  left: 6rem;
  width: 25rem;
  height: 20rem;
}

.content_layout1_para,.content_layout2_para {
  display: flex;
  flex-direction: column;
  width: 50rem;
  height: 30rem;
  justify-content: center;
}
.content_layout1_para h1 ,.content_layout2_para h1{
  margin-top: 6rem;
  text-align: center;
}
.content_layout1_para p,.content_layout2_para p{
  width: 30rem;
  height: 15rem;
  margin: auto;
}


.content_layout2_img img{
    position: absolute;
    top: 39rem;
    left: 47rem;
    width: 25rem;
    height: 20rem;

}

