.container{
  display: flex;
  background-color: black;
  position: sticky;
  top: 0;
  z-index: 3;
  height: 60px;
  margin: auto;
}
#company-Logo{
    margin: auto 0;
    height: 43px;
    width: 43px;
    margin-left: 5px;
    margin-right: 10px;
}
#navbar{
    display: flex;
    list-style: none;
    font-size: 25px ;
    margin: 5px;
}

.navItems{
    margin-right: 15px;
    padding: 3px;
}

.navItems a{
    color: rgb(255, 255, 255);
    text-decoration: none;
    padding: 5px;    
}

#theam{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5%;
    height: 100%;
    margin-left: 40rem;
}
.filter{
    width: 50%;
    height:50%;
    animation-name: icon;
    animation-duration: 1s;
    animation-iteration-count: 1;
    filter: invert(1);
}


@keyframes icon{
    0% {opacity: 0;}
    100% {opacity: 1;}
}