* {
  margin: 0;
  padding: 0;
}

#gallery,#gallery1{
   
    height: 70rem;
    width: 95%;
    display: grid;
    grid-template-columns: repeat(3 , 1fr);
    grid-template-rows: repeat(3 , 1fr);
    row-gap: 2px;
    column-gap: 2px;
    margin: auto;
    margin-top: 30px;
    box-sizing: border-box;   
}
.gallery1{
  grid-template-columns: repeat(4, 1fr) !important;
    grid-template-rows: repeat(4 , 1fr) !important;

  }
.item1{
    grid-row: 1/ span 2;
    width: 30rem;
    height: 40rem ;
}
.image001{
  height: 40rem ;
  width: 30rem;
}

.item2,.image002{

    width: 25.3rem;
    height: 20rem ;
}

.item3{
    grid-column: 1/ span 2;
    height: 30rem;
    width: 100%;

}
.image003{
    height: 30rem;
    width: 100%;
  }

  .item4,.image004{

    width: 25.3rem;
    height: 30rem ;
}




